#define DUCK_SECRET
#include "DuckEngine.h"

/* Secret Squirrel Stuff */
	struct duck_string
	{
		duck_string* previous;
		duck_string* next;
		
		int length;
		int bufferSize;
		char* cString;
	};
	
	duck_string* s_stringList = NULL;
	
	void DuckString_Startup()
	{
	}
	
	void DuckString_Shutdown()
	{
		assert(!s_stringList); // you have not destroyed all created strings
	}
	
	void DuckString_SizeToFit(duck_string* string, int length)
	{
		if (string->bufferSize < length+1)
		{
			if (string->bufferSize == 0)
			{
				string->bufferSize = length+1;
				string->cString = (char*)Duck_Malloc(string->bufferSize);
			}
			else 
			{
				while (string->bufferSize < length+1)
					string->bufferSize = string->bufferSize << 1;
			
				char* oldCString = string->cString;
					string->cString = (char*)Duck_Malloc(string->bufferSize);
					memcpy(string->cString, oldCString, string->length+1);
				Duck_Free(oldCString);
			}
		}
	}
	
/* Public Functionality */
	duck_string* DuckString_Create(const char* cString)
	{
		DUCK_LINKED_LIST_ADD2(duck_string, s_stringList, string);
		
		DuckString_Set(string, cString);
		
		return string;
	}
	
	void DuckString_Destroy(duck_string* string)
	{
		/*DUCK_LINKED_LIST_FIND(duck_string_secret, s_stringList, string, temp);
		if (!temp)
		{
			printf("*** Cannot destroy non existant string");
			assert(false);
		}*/
		
		Duck_Free(string->cString);
		
		
		DUCK_LINKED_LIST_FREE2(s_stringList, string);
	}
	
	int DuckString_GetLength(duck_string* string)
	{
		return string->length;
	}
	
	const char* DuckString_GetCString(duck_string* string)
	{
		return string->cString;
	}
	
	void DuckString_Set(duck_string* string, const char* cString)
	{
		int length = strlen(cString);
		DuckString_SizeToFit(string, length);
		
		char* p = string->cString;
		memcpy(p, cString, length);
		p[length] = '\0';
		string->length = length;
	}
	
	void DuckString_AppendString(duck_string* string1, const duck_string* string2)
	{
		DuckString_SizeToFit(string1, string1->length + string2->length);
		char* p = string1->cString;
		memcpy(p + string1->length, string2->cString, string2->length);
		p[string1->length + string2->length] = '\0';
		string1->length += string2->length;
	}
	
	void DuckString_AppendCString(duck_string* string, const char* cString)
	{
		//printf("before: %s ", string->cString);
		int length = strlen(cString);
		DuckString_SizeToFit(string, string->length + length);
		char* p = string->cString;
		memcpy(p + string->length, cString, length);
		p[string->length + length] = '\0';
		string->length += length;
		//printf("after: %s\n", string->cString);
	}
	
	void DuckString_AppendChar(duck_string* string, const char c)
	{
		//printf("append: %c before: %s(%d:%lu) ", c, string->cString, string->length, strlen(string->cString));
		DuckString_SizeToFit(string, string->length + 1);
		char* p = string->cString;
		p[string->length] = c;
		p[string->length + 1] = '\0';
		string->length++;
		//printf("after: %s(%d)\n", string->cString, string->length);
	}