#define DUCK_SECRET
#include "DuckEngine.h"

/* Secret Squirrel Stuff */
	typedef struct duck_sprite_sheet_sprite duck_sprite_sheet_sprite;
	struct duck_sprite_sheet_sprite
	{
		duck_sprite_sheet_sprite* previous;
		duck_sprite_sheet_sprite* next;
		
		duck_sprite* sprite;
	};


	struct duck_sprite_sheet
	{
		duck_sprite_sheet* previous;
		duck_sprite_sheet* next;
		
		duck_file* file;
		duck_texture* texture;
		
		/*struct duck_sprite_sheet
		{*/
			bool loaded;
			float width;
			float height;
		/*};*/
			
		duck_sprite_sheet_sprite* spriteList;
	};
	
	static duck_sprite_sheet* s_spriteSheetList = NULL;
	
	void DuckSpriteSheet_Startup()
	{
	}
	
	void DuckSpriteSheet_Shutdown()
	{
		assert(!s_spriteSheetList);
	}
	
	void DuckSpriteSheet_Update()
	{
		duck_sprite_sheet* spriteSheet = s_spriteSheetList;
		while (spriteSheet)
		{
			if (!spriteSheet->loaded)
			{
				if (DuckFile_Loaded(spriteSheet->file) && DuckTexture_Loaded(spriteSheet->texture))
				{
					//printf("A %lu\n", temp->file->dataSize);
					bool first = true;
					unsigned int offset = 0;
					
					const char* charData = (const char*)DuckFile_GetData(spriteSheet->file);
				
					//const char* szFile = (const char*)FileManager::FileGetData(pSpriteSheet->hFileHandle);
					//Uint32 nFileSize = FileManager::FileGetSize(pSpriteSheet->hFileHandle);

					duck_string* line = DuckString_Create("");
			
					//printf("~%s\n", line->cString);

					while (offset < DuckFile_GetSize(spriteSheet->file))
					{
						if (charData[offset] == '\n')
						{
							if (first)
							{
								int width = 0;
								int height = 0;
								sscanf(DuckString_GetCString(line), "%d\t%d", &width, &height);
								first = false;

								spriteSheet->width = (float)width;
								spriteSheet->height = (float)height;
							}
							else
							{
								//1_base_0	0	870	80	122	32	4	142	136
								char szName[64];
								int sourceX, sourceY, sourceWidth, sourceHeight, destOffsetX, destOffsetY, destWidth, destHeight;
								sscanf(DuckString_GetCString(line), "%s\t%d\t%d\t%d\t%d\t%d\t%d\t%d\t%d", szName, &sourceX, &sourceY, &sourceWidth, &sourceHeight, &destOffsetX, &destOffsetY, &destWidth, &destHeight);

								
								DUCK_LINKED_LIST_ADD2(duck_sprite_sheet_sprite, spriteSheet->spriteList, sprite);
								sprite->sprite = DuckSprite_Create(spriteSheet->texture, szName, (float)sourceX, (float)sourceY, (float)sourceWidth, (float)sourceHeight, (float)destOffsetX, (float)destOffsetY, (float)destWidth, (float)destHeight);
							}

							DuckString_Set(line, "");
						}
						else
						{
							DuckString_AppendChar(line, charData[offset]);
							//printf("%s\n", line->cString);
						}
						offset++;
					}
					
					//printf("XX\n");
					DuckString_Destroy(line);
					
					
					DuckFile_Close(spriteSheet->file);
					spriteSheet->file = NULL;
					spriteSheet->loaded = true;
				}
			}
			spriteSheet = spriteSheet->next;
		}
	}







/* Public Functionality */
	duck_sprite_sheet* DuckSpriteSheet_Create(const char* fileName)
	{
		DUCK_LINKED_LIST_ADD2(duck_sprite_sheet, s_spriteSheetList, spriteSheet);
		
		//temp->spriteSheet = (duck_sprite_sheet*)Duck_Malloc(sizeof(duck_sprite_sheet));;
		duck_string* path = DuckString_Create("../Resource/");
		DuckString_AppendCString(path, fileName);
		DuckString_AppendCString(path, ".txt");
			spriteSheet->file = DuckFile_Open(DuckString_GetCString(path));
		DuckString_Destroy(path);
		
		spriteSheet->texture = DuckTexture_Create(fileName);
		
		
		
		return spriteSheet;
	}

	void DuckSpriteSheet_Destroy(duck_sprite_sheet* spriteSheet)
	{
		/*DUCK_LINKED_LIST_FIND(duck_sprite_sheet_secret, s_spriteSheetList, spriteSheet, temp);
		assert(temp); // not found!
		*/
		
		while (spriteSheet->spriteList)
		{
			duck_sprite_sheet_sprite* sprite = spriteSheet->spriteList;
			DuckSprite_Destroy(sprite->sprite);
			DUCK_LINKED_LIST_FREE2(spriteSheet->spriteList, sprite);
		}
		
		if (spriteSheet->file)
			DuckFile_Close(spriteSheet->file);
		DuckTexture_Destroy(spriteSheet->texture);

		
		DUCK_LINKED_LIST_FREE2(s_spriteSheetList, spriteSheet);
	}
	
	bool DuckSpriteSheet_Loaded(duck_sprite_sheet* spriteSheet)
	{
		return spriteSheet->loaded;
	}