#define DUCK_SECRET
#include "DuckEngine.h"



/* Secret Squirrel Stuff */
	struct duck_font
	{
		duck_font* previous;
		duck_font* next;

		bool loaded;
		duck_file* file;
		stbtt_fontinfo fontInfo;
	};
	
	static duck_font* s_fontList = NULL;
	
	void DuckFont_Startup()
	{
	}
	
	void DuckFont_Shutdown()
	{
		assert(!s_fontList); // you have not unloaded all fonts!
	}
	
	void DuckFont_Update()
	{
		duck_font* font = s_fontList;
		while (font)
		{
			if (!font->loaded)
			{
				if (DuckFile_Loaded(font->file))
				{
					stbtt_InitFont(&font->fontInfo, DuckFile_GetData(font->file), 0);
					font->loaded = true;
				}
			}
			font = font->next;
		}
	}
	
	stbtt_fontinfo* DuckFont_GetFontInfo(duck_font* font)
	{
		return &font->fontInfo;
	}
	
	
		
/* Public Functionality */
	duck_font* DuckFont_Create(const char* name)
	{
		DUCK_LINKED_LIST_ADD2(duck_font, s_fontList, font);
		
		font->loaded = false;
		
		duck_string* path = DuckString_Create("../Resource/");
		DuckString_AppendCString(path, name);
		DuckString_AppendCString(path, ".ttf");
			font->file = DuckFile_Open(DuckString_GetCString(path));
		DuckString_Destroy(path);
		
		return font;
	}

	void DuckFont_Destroy(duck_font* font)
	{
		//DUCK_LINKED_LIST_FIND2(duck_font, s_fontList, font, found);
		//assert(found);

		DuckFile_Close(font->file);
		
		DUCK_LINKED_LIST_FREE2(s_fontList, font);
	}
	
	bool DuckFont_Loaded(duck_font* font)
	{
		return font->loaded;
	}