#define DUCK_SECRET
#include "DuckEngine.h"

#include "lodepng/lodepng.h"

/* Secret Squirrel Stuff */
	struct duck_texture
	{
		duck_texture* previous;
		duck_texture* next;

		bool loaded;
		GLuint textureId;
		float width;
		float height;
		
		duck_file* file;
	};

	static duck_texture* s_textureList = NULL;
	static duck_texture* s_caulkTexture = NULL;
	
	void DuckTexture_Startup()
	{
		// Caulk
		int x;
		int y;
		int width = 32;
		int height = 32;
		unsigned char* image = (unsigned char*)Duck_Malloc(width*height*4);
		for (y = 0; y < height; y++)
		{
			for (x = 0; x < width; x++)
			{
				unsigned char* p = image + (x + y*width)*4;
				p[0] = x;
				p[1] = 0;
				p[2] = y;
				p[3] = ((x >> 3) + (y >> 3)) % 2 * 128 + 64; // alpha
				//printf("%d\n", p[3]);
			}
		}
		
		s_caulkTexture = DuckTexture_CreateFromMemory(width, height, GL_RGBA, image);
		Duck_Free(image);
	}
	
	void DuckTexture_Shutdown()
	{
		DuckTexture_Destroy(s_caulkTexture);
		
		assert(!s_textureList); // you have not unloaded all textures!
	}





