	void DuckTexture_Update()
	{
		duck_texture* texture = s_textureList;
		while (texture)
		{
			if (!texture->loaded)
			{
				if (DuckFile_Loaded(texture->file))
				{
					//const char* charData = (const char*)texture->file->data;

					assert(DuckFile_GetSize(texture->file) > 0);
				
					unsigned char* imageData = 0;
					unsigned width = 0;
					unsigned height = 0;
					unsigned error = lodepng_decode32(&imageData, &width, &height, DuckFile_GetData(texture->file), DuckFile_GetSize(texture->file));
					assert(error == 0);
					
						texture->loaded = true;
						texture->width = width;
						texture->height = height;
						
						glGenTextures(1, &texture->textureId);
						assert(texture->textureId > 0);
						glBindTexture(GL_TEXTURE_2D, texture->textureId);
						glTexImage2D(GL_TEXTURE_2D, 0, GL_RGBA, texture->width, texture->height, 0, GL_RGBA, GL_UNSIGNED_BYTE, imageData);
						//glTexImage2D(GL_TEXTURE_2D, 0, GL_RGBA, surface->w, surface->h, 0, GL_RGBA, GL_UNSIGNED_BYTE, surface->pixels);
						glTexParameteri(GL_TEXTURE_2D, GL_TEXTURE_MIN_FILTER, GL_LINEAR);
						glTexParameteri(GL_TEXTURE_2D, GL_TEXTURE_MAG_FILTER, GL_LINEAR);
						
						//glTexParameteri(GL_TEXTURE_2D, GL_TEXTURE_MIN_FILTER, GL_NEAREST);
						//glTexParameteri(GL_TEXTURE_2D, GL_TEXTURE_MAG_FILTER, GL_NEAREST);
					
					free(imageData);
					
					DuckFile_Close(texture->file);
					texture->file = NULL;
				}
			}
			texture = texture->next;
		}
	}
	
	duck_texture* DuckTexture_CreateFromMemory(int width, int height, GLenum format, const void* data)
	{
		// GL_RGBA
		DUCK_LINKED_LIST_ADD2(duck_texture, s_textureList, texture);
		
		texture->loaded = true;
		texture->width = width;
		texture->height = height;
	
		glGenTextures(1, &texture->textureId);
		assert(texture->textureId > 0);
		glBindTexture(GL_TEXTURE_2D, texture->textureId);
		// Todo: should be GL_RGBA?
		glTexImage2D(GL_TEXTURE_2D, 0, format, texture->width, texture->height, 0, format, GL_UNSIGNED_BYTE, data); // GL_UNSIGNED_BYTE

		glTexParameteri(GL_TEXTURE_2D, GL_TEXTURE_MIN_FILTER, GL_LINEAR);
		glTexParameteri(GL_TEXTURE_2D, GL_TEXTURE_MAG_FILTER, GL_LINEAR);
		
		//glTexParameteri(GL_TEXTURE_2D, GL_TEXTURE_MIN_FILTER, GL_NEAREST);
		//glTexParameteri(GL_TEXTURE_2D, GL_TEXTURE_MAG_FILTER, GL_NEAREST);
	
		return texture;
	}
	
	duck_texture* DuckTexture_GetCaulkTexture()
	{
		return s_caulkTexture;
	}
	
	

/* Public Functionality */
	duck_texture* DuckTexture_Create(const char* fileName)
	{
		DUCK_LINKED_LIST_ADD2(duck_texture, s_textureList, texture);
		
		texture->loaded = false;
		texture->textureId = s_caulkTexture->textureId;
		texture->width = s_caulkTexture->width;
		texture->height = s_caulkTexture->height;
		
		duck_string* path = DuckString_Create("../Resource/");
		DuckString_AppendCString(path, fileName);
		DuckString_AppendCString(path, ".png");
			texture->file = DuckFile_Open(DuckString_GetCString(path));
		DuckString_Destroy(path);
		
		return texture;
	}
	
	
	
	void DuckTexture_Destroy(duck_texture* texture)
	{
		if (texture->loaded)
			glDeleteTextures(1, &texture->textureId);
		if (texture->file)
			DuckFile_Close(texture->file);

		DUCK_LINKED_LIST_FREE2(s_textureList, texture);
	}
	
	
	bool DuckTexture_Loaded(duck_texture* texture)
	{
		return texture->loaded;
	}
	
	GLuint DuckTexture_GetTextureId(duck_texture* texture)
	{
		return texture->textureId;
	}
	
	float DuckTexture_GetWidth(duck_texture* texture)
	{
		return texture->width;
	}
	
	float DuckTexture_GetHeight(duck_texture* texture)
	{
		return texture->height;
	}