	duck_font* s_font = NULL;
	float s_angle = 0.0f;

	uint32_t s_timer = 0;
	int score = 0;
	//duck_string* question = NULL;
	//duck_string* answer = NULL;
	duck_string* input = NULL;
	//int winTimer = 1;

	#define MODE_INPUT 0
	#define MODE_ATTACK_IN 1
	#define MODE_ATTACK_OUT 2
	#define MODE_DEFEND_IN 3
	#define MODE_DEFEND_OUT 4
	
	static int s_mode = MODE_INPUT;
	
	#define FLOOR 210.0f
	
	#define MONSTER_X 80.0f
	#define PLAYER_X 210.0f
	
	#define GRAVITY 9.0f
	
	#define JUMP_IN_VELOCITY_X 15.0f
	#define JUMP_IN_VELOCITY_Y -30.0f
	
	#define JUMP_OUT_VELOCITY_X -20.0f
	#define JUMP_OUT_VELOCITY_Y -15.0f


	static float s_playerX = PLAYER_X;
	static float s_playerY = FLOOR;
	static float s_playerVelocityX = 0.0f;
	static float s_playerVelocityY = 0.0f;
	
	static float s_monsterX = MONSTER_X;
	static float s_monsterY = FLOOR;
	static float s_monsterVelocityX = 0.0f;
	static float s_monsterVelocityY = 0.0f;

