static int s_lastTime = 0;
static int s_deltaCount = 0;
static int s_deltaSum = 0;
static int s_frameDeltaBuffer = 0;
static bool s_screenDirty = true;


static float s_screenWidth = 320.0f; // 960
static float s_screenHeight = 480.0f; // 480

static const int FRAME_TIME = 33;


static SDL_Window* s_window;




