{
	DuckGui_Shutdown();
	DuckInput_Shutdown();
	DuckFontString_Shutdown();
	DuckFont_Shutdown();
	DuckSpriteSheet_Shutdown();
	DuckSprite_Shutdown();
	//DuckMatrix_Shutdown();
	DuckTexture_Shutdown();
	DuckFile_Shutdown();
	
	//xglfwTerminate();
	
    SDL_Quit();
    
    DuckMemory_Shutdown();
}


