#define DUCK_SECRET
#include "DuckEngine.h"

/* Secret Squirrel Stuff */
	struct duck_file
	{
		duck_file* previous;
		duck_file* next;
		
		duck_string* fileName;
		FILE* fileStream;
		
		bool loaded;
		unsigned long size;
		void* data;
	};

	static duck_file* s_fileList = NULL;
	
	void DuckFile_Startup()
	{
	}
	
	void DuckFile_Shutdown()
	{
		assert(!s_fileList); // you have not closed all open files
	}
	
	void DuckFile_Update()
	{
		#if !defined(EMSCRIPTEN)
			duck_file* file = s_fileList;
			while (file)
			{
				if (!file->loaded)
				{
					// do cool things
					file->fileStream = fopen(DuckString_GetCString(file->fileName), "rb");
					if (file->fileStream)
					{
						fseek(file->fileStream, 0, SEEK_END);
						file->size = ftell(file->fileStream);
						fseek(file->fileStream, 0, SEEK_SET);
						file->data = Duck_Malloc(file->size);

						fread(file->data, file->size, 1, file->fileStream);

						fclose(file->fileStream);
					}
					file->loaded = true;
				}
				file = file->next;
			}
		#endif
	}


	#if defined(EMSCRIPTEN)
		void OnComplete(void* userPointer, void* data, int dataSize)
		{
			//printf("FILE LOAD\n");
			duck_file* file = (duck_file*)userPointer;

			file->size = dataSize;
			file->data = Duck_Malloc(file->size);
			memcpy(file->data, data, file->size);

			file->loaded = true;
		}

		void OnError(void* userPointer)
		{
			duck_file* file = (duck_file*)userPointer;
			printf("FILE LOAD ERROR! %s\n", DuckString_GetCString(file->fileName));

			file->loaded = true;
		}
	#endif


/* Public Functionality */
	duck_file* DuckFile_Open(const char* fileName)
	{
		DUCK_LINKED_LIST_ADD2(duck_file, s_fileList, file);
		
		//temp->file = (duck_file*)Duck_Malloc(sizeof(duck_file));
		//temp->file->data = NULL;
		file->fileName = DuckString_Create(fileName);
		//temp->fileStream = NULL;

		#if defined(EMSCRIPTEN)
			duck_string* temp = DuckString_Create(""); //"http://localhost:8082/Resource/");
			DuckString_AppendString(temp, file->fileName);
			emscripten_async_wget_data(DuckString_GetCString(temp), file, OnComplete, OnError);
			DuckString_Destroy(temp);
		#endif
		
		return file;
	}
	
	void DuckFile_Close(duck_file* file)
	{	
		//DUCK_LINKED_LIST_FIND(duck_file, s_fileList, file, temp);
		//assert(temp); // tried to close non existant file
		
		Duck_Free(file->data);
		DuckString_Destroy(file->fileName);
		
		DUCK_LINKED_LIST_FREE2(s_fileList, file);
	}
	
	bool DuckFile_Loaded(duck_file* file)
	{
		return file->loaded;
	}
	
	unsigned long DuckFile_GetSize(duck_file* file)
	{
		return file->size;
	}
	
	const void* DuckFile_GetData(duck_file* file)
	{
		return file->data;
	}