#define DUCK_SECRET
#include "DuckEngine.h"

/* Secret Squirrel Stuff */
	typedef struct duck_key_state duck_key_state;
	struct duck_key_state
	{
		bool down;
		bool up;
	};

	static duck_key_state s_keyState[DUCK_NUM_KEY];
	
	static uint32_t* s_unicodeInputBuffer;
	static uint32_t s_unicodeInputBufferSize;
	static uint32_t s_numUnicodeInput;
	
	static float s_mouseX;
	static float s_mouseY;
	
	
	
	void DuckInput_Startup()
	{
		s_unicodeInputBufferSize = 128;
		s_unicodeInputBuffer = Duck_Malloc(sizeof(uint32_t)*s_unicodeInputBufferSize);
		s_numUnicodeInput = 0;
		
		s_mouseX = 0.0f;
		s_mouseY = 0.0f;
		
		uint32_t i;
		for (i = 0; i < DUCK_NUM_KEY; i++)
		{
			s_keyState[i].down = false;
			s_keyState[i].up = true;
		}
	}
	
	void DuckInput_Shutdown()
	{
		Duck_Free(s_unicodeInputBuffer);
	}
	
	void DuckInput_Event(SDL_Event* event)
	{
		if(event->type == SDL_KEYDOWN)
		{
			// TODO: not unicodes!
			const char* temp = SDL_GetScancodeName(event->key.keysym.sym); //SDL_GetKeyName(event->key.keysym.sym);
			//printf("%s\n", temp);
			if (temp[0] >= 'A' && temp[0] <= 'Z' && temp[1] == '\0')
			{
				assert(s_numUnicodeInput < s_unicodeInputBufferSize);
				s_unicodeInputBuffer[s_numUnicodeInput] = (uint32_t)temp[0];
				s_numUnicodeInput++;
			}
			else
			{
				switch (event->key.keysym.sym)
				{
					case SDL_SCANCODE_RETURN:
					case SDL_SCANCODE_RETURN2:
						s_keyState[DUCK_KEY_ENTER].down = true;
						break;
					default:
						break;
				}
			}
		}
		else if(event->type == SDL_KEYUP)
		{
			switch (event->key.keysym.sym)
			{
				case SDL_SCANCODE_RETURN:
				case SDL_SCANCODE_RETURN2:
					s_keyState[DUCK_KEY_ENTER].up = true;
					break;
				default:
					break;
			}
		}
	}




/* pubs */
	bool DuckInput_GetKeyDown(uint32_t key)
	{
		assert(key < DUCK_NUM_KEY);
		return s_keyState[key].down;
	}

	bool DuckInput_GetKeyPressed(uint32_t key)
	{
		assert(key < DUCK_NUM_KEY);
		if (s_keyState[key].up && s_keyState[key].down)
		{
			s_keyState[key].up = false;
			s_keyState[key].down = false;
			return true;
		}
		return false;
	}

	uint32_t DuckInput_GetUnicodeInput()
	{
		uint32_t returnable = 0;
		if (s_numUnicodeInput > 0)
		{
			returnable = s_unicodeInputBuffer[0];
			s_numUnicodeInput--;
			memcpy(s_unicodeInputBuffer, s_unicodeInputBuffer+1, s_numUnicodeInput);
		}
		return returnable;
	}
	
	void DuckInput_ClearUnicodeInput()
	{
		s_numUnicodeInput = 0;
	}
	
	float DuckInput_GetMouseX()
	{
		return s_mouseX;
	}
	
	float DuckInput_GetMouseY()
	{
		return s_mouseY;
	}
		
		
		
