#define DUCK_SECRET
#include "DuckEngine.h"

/* Secret Squirrel Stuff */
	typedef struct duck_allocation duck_allocation;
	struct duck_allocation
	{
		void* pointer;
		char* file;
		int line;
		duck_allocation* next;
	};
	
	static bool s_initialized = false;
	static duck_allocation* s_allocationList = NULL;
	


	void DuckMemory_Startup()
	{
		s_initialized = true;
	}
	
	void DuckMemory_Shutdown()
	{
		if (s_allocationList)
		{
			printf("********************** Leaks Detected ************************\n");
			
			duck_allocation* temp = s_allocationList;
			while (temp)
			{
				printf("%s (%d)\n", temp->file, temp->line);
				temp = temp->next;
			}
		}
	}




/* Public Functionality */
	void* DuckMemory_Malloc(size_t size, const char* file, int line)
	{
		assert(s_initialized); // you have not called Duck_Startup();
		
		duck_allocation* temp = s_allocationList;
		if (temp)
		{
			while (temp->next)
				temp = temp->next;
			temp->next = (duck_allocation*)malloc(sizeof(duck_allocation));
			temp = temp->next;
		}
		else
		{
			s_allocationList = (duck_allocation*)malloc(sizeof(duck_allocation));
			temp = s_allocationList;
		}
		memset(temp, 0, sizeof(duck_allocation));
		temp->pointer = malloc(size);
		temp->file = malloc(strlen(file)+1);
		strcpy(temp->file, file);
		temp->line = line;
		
		return temp->pointer;
	}
	
	void DuckMemory_Free(void* pointer)
	{
		if (!pointer)
			return;
		
		if (s_allocationList)
		{
			if (s_allocationList->pointer == pointer)
			{
				free(s_allocationList->pointer);
				
				duck_allocation* temp = s_allocationList->next;
				free(s_allocationList);
				s_allocationList = temp;
				return;
			}
			else
			{
				duck_allocation* p = s_allocationList;
				while (p->next)
				{
					if (p->next->pointer == pointer)
					{
						duck_allocation* temp = p->next;
						//printf("%x %s (%d)\n", (unsigned int)pointer, temp->file, temp->line);
						p->next = p->next->next;
						free(temp->pointer);
						free(temp);
						return;
					}
					p = p->next;
				}
			}
		}
		printf("Unknown pointer: %x\n", (unsigned int)pointer);
		assert(false);
	}