#define DUCK_SECRET
#include "DuckEngine.h"

/* Secret Squirrel */
	GLuint DuckShader_LoadShader(const char* source, GLenum type)
	{
		GLuint shader = glCreateShader(type);
		glShaderSource(shader, 1, &source, NULL);
		glCompileShader(shader);
		// Check the result of the compilation
		GLint test;
		glGetShaderiv(shader, GL_COMPILE_STATUS, &test);
		
		//assert(test);
		if(!test) {
			
			char temp[1024];

			//std::vector<char> compilation_log(512);
			glGetShaderInfoLog(shader, 1024, NULL, temp);
			//std::cerr << &compilation_log[0] << std::endl;
			printf("Shader compilation failed with this message: %s\n", temp);
			assert(false);
		}
		return shader;
	}
	
	GLuint DuckShader_LoadProgram(const char* vertexSource, const char* fragmentSource)
	{
		GLuint program = glCreateProgram();
		GLuint vertexShader = DuckShader_LoadShader(vertexSource, GL_VERTEX_SHADER);
		GLuint fragmentShader = DuckShader_LoadShader(fragmentSource, GL_FRAGMENT_SHADER);
		
		glAttachShader(program, vertexShader);
		glAttachShader(program, fragmentShader);

		glDeleteShader(vertexShader);
		glDeleteShader(fragmentShader);

		glLinkProgram(program);
		glUseProgram(program);

		return program;
	}