#define DUCK_SECRET
#include "DuckEngine.h"

/* Secret Squirrel Stuff */
	static duck_gui* s_rootGui = NULL;
	static duck_gui* s_focusGui = NULL;
	
	void DuckGui_Startup()
	{
		s_rootGui = DuckGui_Create(NULL);
	}
	
	void DuckGui_Shutdown()
	{
		DuckGui_Destroy(s_rootGui);
	}
	
	void DuckGui_Update()
	{
		s_rootGui->update(s_rootGui, 0.0f, 0.0f);
	}
	
	void DuckGui_Render()
	{
		s_rootGui->render(s_rootGui, 0.0f, 0.0f);
	}
	



/* Functions */
	duck_gui* DuckGui_Create(duck_gui* parent)
	{
		duck_gui* gui = (duck_gui*)Duck_Malloc(sizeof(duck_gui));
			memset(gui, 0, sizeof(duck_gui));
			gui->update = &DuckGui_DefaultUpdate;
			gui->render = &DuckGui_DefaultRender;
			gui->addChild = &DuckGui_DefaultAddChild;
			gui->removeChild = &DuckGui_DefaultRemoveChild;
		
		if (parent)
			parent->addChild(parent, gui);
		
		return gui;
	}

	void DuckGui_Destroy(duck_gui* gui)
	{
		duck_gui_child* child = gui->childList;
		while (child)
		{
			duck_gui_child* temp = child;
			child = child->next;
			DuckGui_Destroy(temp->child);
			DUCK_LINKED_LIST_FREE(temp);
		}
		gui->childList = NULL;
		
		Duck_Free(gui);
	}


	duck_gui* DuckGui_GetRootGui()
	{
		return s_rootGui;
	}
	
	duck_gui* DuckGui_GetFocusGui()
	{
		return s_focusGui;
	}
	
	void DuckGui_SetFocusGui(duck_gui* focusGui)
	{
		s_focusGui = focusGui;
	}

	

// Default Functions
	void DuckGui_DefaultUpdate(duck_gui* self, float x, float y)
	{
		duck_gui_child* child = self->childList;
		while (child)
		{
			child->child->update(child->child, x + self->x, y + self->y);
			child = child->next;
		}
	}

	void DuckGui_DefaultRender(duck_gui* self, float x, float y)
	{
		duck_gui_child* child = self->childList;
		while (child)
		{
			child->child->render(child->child, x + self->x, y + self->y);
			child = child->next;
		}
	}
	
	void DuckGui_DefaultAddChild(duck_gui* self, duck_gui* child)
	{
		DUCK_LINKED_LIST_ADD(duck_gui_child, self->childList, temp);
		temp->child = child;
	}
	
	void DuckGui_DefaultRemoveChild(duck_gui* self, duck_gui* child)
	{
		DUCK_LINKED_LIST_FIND(duck_gui_child, self->childList, child, temp);
		assert(temp); // tried to remove non existant child
		
		DUCK_LINKED_LIST_FREE(temp);
	}